import React from 'react'
import styles from './order-details.module.css'
import img from '../../images/done.png'
import { useSelector } from '../../store'

function OrderDetails() {
    const orderValue = useSelector((state) => state.order.number)

    return (
        <div className={`${styles.wrap} mt-30 mb-30`}>
            <p className={`${styles.number} text text_type_digits-large mb-8`}>{orderValue}</p>
            <p className='text text_type_main-medium mb-15'> </p>
            <img className='mb-15' src={img} alt='done' />
            <p className='cook text text_type_main-default mb-2'>   </p>
            <p className='expect text text_type_main-default text_color_inactive'>    </p>
        </div>
    )
}

export default OrderDetails
