import React, { useEffect, useState } from 'react'
import { Link, Redirect, useLocation } from 'react-router-dom'
import { Input, PasswordInput, Button } from '@ya.praktikum/react-developer-burger-ui-components'
import { checkUser, loginUser } from '../../store/actions/auth'
import { useDispatch, useSelector } from '../../store'
import styles from './login.module.css'
import Preloader from '../../components/preloader/preloader'
import { getTokens } from '../../helpers/auth'
import { ILocation } from '../../utils/type'

export const LoginPage = () => {
    const dispatch = useDispatch()
    const location = useLocation<ILocation>()
    const profile = useSelector((state) => state.profile)
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const { accessToken, refreshToken } = getTokens()

    const postLogin = (e: { preventDefault: () => void }) => {
        e.preventDefault()

        dispatch(loginUser({ email, password }))
    }

    useEffect(() => {
        dispatch(checkUser())
    }, [dispatch, accessToken, refreshToken])

    if (!!profile.user.success) {
        return <Redirect to={ location.state?.from || '/' } />
    }

    if (profile.loading) {
        return <Preloader />
    }

    return (
        <div className={styles.formWrap}>
            <form
                className={styles.form}
                onSubmit={ postLogin }
            >
                <div className={`${styles.title} text text_type_main-medium mb-6`}></div>
                <div className={`${styles.inputWrap} mb-6`}>
                    <Input
                        value={ email }
                        type="text"
                        name={'email'}
                        placeholder="email"
                        onChange={e => setEmail(e.target.value)}
                    />
                </div>
                <div className={`${styles.inputWrap} mb-6`}>
                    <PasswordInput
                        value={ password }
                        name={'password'}
                        onChange={e => setPassword(e.target.value)}
                    />
                </div>
                <Button
                    type='primary'
                    size='medium'
                >
                    
                </Button>
                <div className={'text text_type_main-default text_color_inactive mt-20'}>
                     -  ?
                    <Link to="/register" className={`${styles.link} ml-2`}></Link>
                </div>
                <div className={'text text_type_main-default text_color_inactive mt-4'}>
                     ?
                    <Link to="/forgot-password" className={`${styles.link} ml-2`}> </Link>
                </div>
            </form>
        </div>
    )
}
